<template>
   <v-container>
    <v-data-table
    :headers="headers"
    :items="todo"
    :items-per-page="5"
    class="elevation-1"
    ></v-data-table>
    <v-snackbar
    v-model="snackbar"
  >
    {{ text }}

    <template v-slot:action="{ attrs }">
      <v-btn color="pink" text v-bind="attrs" @click="snackbar = false">
        fechar
      </v-btn>
    </template>
  </v-snackbar>
   </v-container>
</template>
<script>

import { mapGetters,mapActions } from 'vuex'
    
export default {
    data() {
        return {
            snackbar: false,
            text: `Novos Dados de carregamento salvo`,
            headers: [
          {
            text: '#',
            align: 'start',
            sortable: false,
            value: 'id_carregamento',
          },
          { text: 'nome', value: 'nome' },
          { text: 'tipo', value: 'tipo' },
          { text: 'tamanho', value: 'tamanho' },
          { text: 'data', value: 'datacad' },
        ],
        }
    },
    computed: {
        ...mapGetters({
            todo:'carregamento/todo',
        })
    },
    watch: {
        todo:{
            handler(oldVal, val){
                this.snackbar = true
            }
        }
    },
    methods: {
        ...mapActions({
            get:'carregamento/getUploadHistoryByIdUsuario',

        }),
    },
    mounted() {
        this.get()
    },
    
}
</script>